@font-face {
	font-family: 'Forum';
	src: url('../../fonts/forum.eot');
	src: local('☺'), url('../../fonts/forum.woff') format('woff'), url('../../fonts/forum.ttf') format('truetype'), url('../../fonts/forum.svg') format('svg');
	font-weight: 400;
	font-style: normal;
}
    body {
        font-family: 'Forum';
        font-weight: 400;
        width: 100%;
        color: #ffffff;
        background-color: #3B181E;
     }
    .container {
         max-width: 1758px;
         margin: 0 auto;
         padding: 0 90px;
         @media screen and (max-width: 768px) {
             padding: 0 50px;
             max-width: 768px;
         }
         @media screen and (max-width: 768px) {
            padding: 0 15px;
        }
     }
     .btn-ig {
        background: url('../img/inst.png') no-repeat center;
        background-size: 100%;
        width: 25px;
        height: 25px;
        display: block;
        &:hover {
            background: url('../img/igh.png') no-repeat center;
        }
    }
    .btn-fb {
        background: url('../img/fb.png') no-repeat center;
        background-size: 100%;
        margin-top: 1.64em;
        width: 25px;
        height: 25px;
        display: block;
        &:hover {
            background: url('../img/fbh.png') no-repeat center;
        }
        @media screen and (max-width: 768px) {
            margin-top: 0;
        }
    }
    .btn-wp {
        background: url('../img/whatsapp.png') no-repeat center;
        background-size: 100%;
        margin-top: 1.64em;
        width: 27px;
        height: 27px;
        display: block;
        &:hover {
            background: url('../img/wph.png') no-repeat center;
        }
        @media screen and (max-width: 768px) {
            margin-top: 0;
        }
    }
    .btn-vk {
        background: url('../img/vk.png') no-repeat center;
        background-size: 100%;
        margin-top: 1.64em;
        width: 25px;
        height: 25px;
        display: block;
        &:hover {
            background: url('../img/vkh.png') no-repeat center;
        }
        @media screen and (max-width: 768px) {
            margin-top: 0;
        }
    }
     .header {
        background: url('../img/promo.png') no-repeat center;
        background-size: cover;
        padding-top: 6.21em;
        position: relative;
        @media screen and (max-width: 768px) {
            padding-bottom: 10em;
        }
        @media screen and (max-width: 420px) {
            padding-top: 4.21em;
            padding-bottom: 7em;
        }
        .container {
            position: relative;
            .page-header {
                display: flex;
                justify-content: space-between;
                .header-burger {
                    cursor: pointer;
                    width: 104px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    @media screen and (max-width: 420px) {
                        width: 37px;
                    }
                    p {
                        text-transform: uppercase;
                        font-size: 1.43em;
                        letter-spacing: 0.06em;
                        @media screen and (max-width: 420px) {
                            display: none;
                        }
                    }
                    .burger {
                        display: block;
                        position: relative;
                        z-index: 4;
                        width: 23px;
                        height: 10px;
                        &::before,
                        &::after {
                            content: '';
                            background-color: #ffffff;
                            height: 1px;
                            position: absolute;
                            width: 100%;
                            left: 0;
                        }
                        &:before {
                            top: 0;
                        }
                        &:after {
                            bottom: 0;
                        }
                    }
                }
            }
            .promo {
                text-align: center;
                margin-top: 14.14em;
                @media screen and (max-width: 420px) {
                    margin-top: 9em;
                }
                h1 {
                    font-size: 8.57rem;
                    text-transform: uppercase;
                    line-height: 1.1;
                    letter-spacing: 0.09em;
                    @media screen and (max-width: 1400px) {
                        font-size: 7.35em;
                    }
                    @media screen and (max-width: 1200px) {
                        font-size: 5.35em;
                    }
                    @media screen and (max-width: 768px) {
                        font-size: 4em;
                    }
                    @media screen and (max-width: 450px) {
                        font-size: 2.14em;
                    }
                }
                .btn {
                    text-transform: uppercase;
                    margin-top: 5.67em;
                    margin-bottom: 1em;
                    color: #ffffff;
                    letter-spacing: 0.15em;
                    border: 1px solid #ffffff;
                    font-size: 0.86rem;
                    line-height: 1.08;
                    padding: 1.58em 2.5em 1.14em 2.5em;
                    background: rgba(0, 0, 0, 0.4);
                    &:hover {
                        background: #ffffff;
                        color: #000000;
                    }
                }
                .btn-scroll {
                    margin-top: 11.21em;
                    @media screen and (max-width: 768px) {
                        display: none;
                    }
                    img {
                        cursor: pointer;
                    }
                }
            }
            .social-btn {
                position: absolute;
                bottom: 1em;
                @media screen and (max-width: 768px) {
                   position: relative;
                    z-index: 1;
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    margin-top: 10em;
                }
                @media screen and (max-width: 420px) {
                    margin-top: 6em;
                }
            }
            .catalog-scroll {
                position: absolute;
                right: 0;
                bottom: 8em;
                transform: rotate(90deg);
                cursor: pointer;
                @media screen and (max-width: 768px) {
                    display: none;
                }
                a {
                    font-size: 1.43rem;
                    color: #ffffff;
                    letter-spacing: 0.06em;
                    line-height: 1.1;
                    position: relative;
                    padding-right: 2em;
                    &:after {
                        content: '';
                        background: url('../img/arrow.png') no-repeat;
                        transform: rotate(-90deg);
                        display: inline-block;
                        top: -2px;
                        position: absolute;
                        right: 0;
                        width: 20.42px;
                        height: 20.42px;
                    }
                }
            }
        }
        .bottom-gradient {
            margin-top: 7em;
            height: 75px;
            width: 100%;
            background: linear-gradient(0deg, #3B181E 0%, rgba(59, 24, 30, 0) 100%);
            @media screen and (max-width: 768px) {
                display: none;
            }
        }   
        .burger-menu {
            display: none;
            padding-top: 6.21em;
            position: absolute;
            z-index:5;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: #3B181E;
            @media screen and (max-width: 420px) {
                padding-top: 4.21em;
            }
            .burger-menu-header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                .exit {
                    width: 28px;
                    height: 28px;
                    position: relative;
                    cursor: pointer;
                    &:before,
                    &:after {
                        content: ' ';
                        height: 29px;
                        width: 2px;
                        background-color: rgba(255, 255, 255, 0.5);
                        position: absolute;
                        top: 0;
                        left: 12px;
                    }
                    &:before {
                        transform: rotate(45deg);
                    }
                    &:after {
                        transform: rotate(-45deg);
                    }
                }
            }
            .burger-menu-body {
                text-align: center;
                margin-top: 10.25em;
                @media screen and (max-width: 420px) {
                    margin-top: 7em;
                }
                ul {
                    li {
                        margin-top: 5.43em;
                        a {
                            letter-spacing: 0.09em;
                            text-transform: uppercase;
                            color: rgba(255, 255, 255, 0.5);
                            font-size: 2.5rem;
                            line-height: 1.11;
                            &:hover {
                                color: #ffffff;
                            }
                            @media screen and (max-width: 768px) {
                                font-size: 2.14rem;
                            }
                            @media screen and (max-width: 420px) {
                                font-size: 1.1rem;
                            }
                        }
                    }
                }
            }
            .social-btn {
                bottom: 0;
                @media screen and (max-width: 768px) {
                    display: none;
                }
            }
        } 
     }
     .catalog {
         padding-top: 8em;
         text-align: center;
         @media screen and (max-width: 420px) {
            text-align: left;
            padding-top: 5em;
        }
         .container {
             position: relative;
            h2 {
                font-size: 2.14rem;
                line-height: 1.65;
                letter-spacing: 0.06em;
                text-transform: uppercase;
                color: #ffffff;
                position: relative;
                display: inline-block;
                @media screen and (max-width: 420px) {
                    font-size: 1.43em;
                }
                &:after {
                    display: block;
                    position: absolute;
                    content: '';
                    height: 2px;
                    width: 40%;
                    left: 32%;
                    bottom: -0.5em;
                    background: #ffffff;
                    @media screen and (max-width: 420px) {
                        display: none;
                    }
                }
             }
             .catalog-box {
                 display: flex;
                 justify-content: space-around;
                 margin-top: 9.64em;
                 @media screen and (max-width: 900px) {
                    display: none;
                }
                 .catalog-item {
                     flex-basis: 22%;
                     position: relative;
                     z-index: 3;
                     &:hover {
                         cursor: pointer;
                     }
                     .btn {
                         display: none;
                         position: absolute;
                         bottom: 20px;
                         left: 20px;
                         border: 1px solid #ffffff;
                         font-size: 0.86rem;
                         line-height: 1.08;
                         padding: 1.58em 2.5em 1.14em 2.5em;
                         background: #ffffff;
                         color: #000000;
                         text-align: center;
                         letter-spacing: 0.15em;
                         text-transform: uppercase;
                         @media screen and (max-width: 1160px) {
                            font-size: 0.76rem;
                            padding: 1.38em 2em 1em 2em;
                        }
                        @media screen and (max-width: 1160px) {
                            font-size: 0.76rem;
                            padding: 1em 1.6em 0.89em 1.6em;
                        }
                        @media screen and (max-width: 960px) {
                            font-size: 0.66rem;
                            padding: 0.9em 1.4em 0.79em 1.4em;
                        }
                     }
                     p {
                         position: absolute;
                         bottom: 1em;
                         left: 1.4em;
                         letter-spacing: 0.1em;
                         text-transform: uppercase;
                         font-size: 1.14rem;
                         line-height: 1.6875;
                         text-align: left;
                         opacity: 0.9;
                     }
                     img {
                         width: 100%;     
                     }
                 }
             }
             .catalog-slider {
                 display: none;
                 @media screen and (max-width: 900px) {
                    display: block;
                    padding-bottom: 2em;
                    margin-top: 4em;
                    position: relative;
                }
                .slick-list {
                    overflow: hidden;
                }
                .slick-track {
                    display: flex;
                    align-items: space-between;
                 }
                .slick-dots {
                    display: flex;
                    justify-content: center;
                    position: absolute;
                    left: 38%;
                    bottom: -40px;
                    @media screen and (max-width: 420px) {
                        position: absolute;
                        margin: auto;
                        top: 100%; left: 0; bottom: 0; right: 0;
                    }
                    
                    li {
                        margin-left: 5%;
                        :last-child {
                            margin-left: 0;
                        }
                        &.slick-active {
                            button {
                                opacity: 1;
                            }
                        }
                        button {
                            font-size: 0;
                            width: 75px;
                            height: 2px;
                            background-color: #ffffff;
                            opacity: 0.7;
                            @media screen and (max-width: 420px) {
                                width: 60px; 
                            }
                        }
                    }
                 }
                .catalog-slider-item {
                    position: relative;
                    padding: 0 10px;
                    @media screen and (max-width: 420px) {
                        padding: 0;
                    }
                    p {
                        position: absolute;
                        bottom: 1em;
                        left: 1em;
                        letter-spacing: 0.1em;
                        text-transform: uppercase;
                        font-size: 1.14rem;
                        line-height: 1.6875;
                        text-align: left;
                        opacity: 0.9;
                    }
                    img {
                        width: 100%;
                    }
                }
             }
             .catalog-bag {
                 position: absolute;
                 margin: auto;
                 z-index: 1;
                 top: 3em; left: 0; bottom: 0; right: 0;
                 @media screen and (max-width: 768px) {
                    display: none;
                }
                 p {
                    color: #ffffff;
                    opacity: 0.03;
                    font-size: 1200%;
                    text-transform: uppercase;
                    line-height: 1.1;
                    letter-spacing: 0.06em;
                    text-align: center;
                 }
             }
         }  
     }
     .special-offer {
         padding-top: 12.14em;
         padding-bottom: 12.14em;
         @media screen and (max-width: 768px) {
            padding-bottom: 6em;
         }
         @media screen and (max-width: 420px) {
            padding-top: 7.14em;
        }
        
         .special-offer-box {
             display: flex;
             justify-content: center;
             align-items: center;
             @media screen and (max-width: 768px) {
                flex-direction: column;
                align-items: flex-start;
            }
             .text-item {
                 flex-basis: 25%;
                 @media screen and (max-width: 768px) {
                    flex-basis: auto;
                    order: 2;
                    width: 100%;
                }
                h2 {
                    font-size: 2.14rem;
                    line-height: 1.65;
                    letter-spacing: 0.06em;
                    text-transform: uppercase;
                    color: #ffffff;
                    position: relative;
                    display: inline-block;
                    @media screen and (max-width: 768px) {
                        margin-top: 2em;
                    }
                    @media screen and (max-width: 420px) {
                        font-size: 1.43em;
                    }
                    &:after {
                        display: block;
                        position: absolute;
                        content: '';
                        height: 2px;
                        width: 40%;
                        left: 0;
                        bottom: -0.5em;
                        background: #ffffff;
                        @media screen and (max-width: 768px) {
                            left: 30%;
                        }
                        @media screen and (max-width: 420px) {
                            display: none;
                        }
                    }
                 }
                 p {
                     font-size: 1rem;
                     line-height: 1.71;
                     margin-top: 2.86em;
                 }
                 .btn-offer {
                     background: #010001;
                     margin-top: 4.09em;
                     color: #ffffff;
                     letter-spacing: 0.15em;
                     text-transform: uppercase;
                     font-size: 0.79rem;
                     line-height: 1.09;
                     padding: 1.55em 2.73em 1.36em 2.82em;
                     &:hover {
                         background-color: #ffffff;
                         color: #000000;
                     }
                 }
             } 
             .img-item {
                 flex-basis: 39.65%;
                 margin-left: 9.71em;
                 @media screen and (max-width: 768px) {
                    flex-basis: auto;
                    margin-left: 0;
                    width: 100%;
                    order: 1;
                    height: 10%;
                }
                 img {
                     width: 100%;
                     @media screen and (max-width: 768px) {
                        max-width: 100%;
                       
                    }
                 }
             }     
         }  
     }
     footer {
            border-top: 1px solid rgba(255, 255, 255, 0.2);
            padding-top: 3em;
            padding-bottom: 2.14em;
         .footer-top {
             ul {
                 display: flex;
                 align-items: center;
                 justify-content: center;
                 @media screen and (max-width: 768px) {
                    flex-direction: column;
                }
                li {
                    margin-left: 2%;
                    @media screen and (max-width: 768px) {
                        margin-left: 0;
                        margin-top: 2em;
                    }
                    &:first-child {
                        margin-left: 0;
                        margin-top: 0;
                    }
                    a {
                       color: rgba(255, 255, 255, 0.5);
                       font-size: 1.14rem;
                       line-height: 1.125;
                       &:hover {
                         color: rgba(255, 255, 255, 1);
                       }
                       @media screen and (max-width: 768px) {
                        font-size: 1.43em;
                    }
                    }
                }
             }
         }
         .footer-bottom {
             padding-top: 2.14em;
             display: flex;
             align-items: center;
             justify-content: center;
             @media screen and (max-width: 420px) {
                justify-content: space-around;
                padding-top: 4em;
            }
             a {
                 margin-left: 5%;
                 @media screen and (max-width: 420px) {
                    margin-left: 0;
                }
             }
             .btn-fb,
             .btn-wp,
             .btn-vk {
                 margin-top: 0;
             }
         }
     }








